<template>
  <ParagraphWithCarousel
    title="Typography"
    subtitle="Readable and Easy to Understand"
    paragraph="I used SF Pro Text because I wanted to stay consistent with the iOS guidelines for the main typeface. I used Righteous for the logo font because it is clean, readable, and has that retro look for the fitted app style"
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import fitted_typography1 from "../../assets/img/fitted_typography1.jpg";
import fitted_typography2 from "../../assets/img/fitted_typography2.jpg";
import fitted_typography3 from "../../assets/img/fitted_typography3.jpg";
import fitted_typography4 from "../../assets/img/fitted_typography4.jpg";
import fitted_typography5 from "../../assets/img/fitted_typography5.jpg";
import fitted_typography6 from "../../assets/img/fitted_typography6.jpg";
import fitted_typography7 from "../../assets/img/fitted_typography7.jpg";
import fitted_typography8 from "../../assets/img/fitted_typography8.jpg";
import fitted_typography9 from "../../assets/img/fitted_typography9.jpg";
import fitted_typography10 from "../../assets/img/fitted_typography10.jpg";
import fitted_typography11 from "../../assets/img/fitted_typography11.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: fitted_typography1},
        {img: fitted_typography2},
        {img: fitted_typography3},
        {img: fitted_typography4},
        {img: fitted_typography5},
        {img: fitted_typography6},
        {img: fitted_typography7},
        {img: fitted_typography8},
        {img: fitted_typography9},
        {img: fitted_typography10},
        {img: fitted_typography11},
      ],
      containerStyle: {
        height: "600px"
      },
      imgStyle: {
        padding: "24px",
        height: "100%"
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



