    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import fitted_highfidelity1 from "../../assets/img/fitted_highfidelity1.jpg";
import fitted_highfidelity2 from "../../assets/img/fitted_highfidelity2.jpg";
import fitted_highfidelity3 from "../../assets/img/fitted_highfidelity3.jpg";
import fitted_highfidelity4 from "../../assets/img/fitted_highfidelity4.jpg";
import fitted_highfidelity5 from "../../assets/img/fitted_highfidelity5.jpg";
import fitted_highfidelity6 from "../../assets/img/fitted_highfidelity6.jpg";
import fitted_highfidelity7 from "../../assets/img/fitted_highfidelity7.jpg";
import fitted_highfidelity8 from "../../assets/img/fitted_highfidelity8.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: fitted_highfidelity1},
        {img: fitted_highfidelity2},
        {img: fitted_highfidelity3},
        {img: fitted_highfidelity4},
        {img: fitted_highfidelity5},
        {img: fitted_highfidelity6},
        {img: fitted_highfidelity7},
        {img: fitted_highfidelity8},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

