<template>
  <ParagraphWithCarousel
    title="Mid Fidelity Wireframes"
    subtitle="Composition & Visual Hierarchy = More Intuitive"
    paragraph="Considering the key action I wanted users to take, I applied visual hierarchy to my web app screens. I focused on size, contrast, boldness, positioning, and then refining my design by applying good spacing. After arranging the elements into a visual hierarchy that makes sense to my users, I made my design more recognizable and clear to users."
