import fitted_midfidelity1 from "../../assets/img/fitted_midfidelity1.jpg";
import fitted_midfidelity2 from "../../assets/img/fitted_midfidelity2.jpg";
import fitted_midfidelity3 from "../../assets/img/fitted_midfidelity3.jpg";
import fitted_midfidelity4 from "../../assets/img/fitted_midfidelity4.jpg";
import fitted_midfidelity5 from "../../assets/img/fitted_midfidelity5.jpg";
import fitted_midfidelity6 from "../../assets/img/fitted_midfidelity6.jpg";
import fitted_midfidelity7 from "../../assets/img/fitted_midfidelity7.jpg";
import fitted_midfidelity8 from "../../assets/img/fitted_midfidelity8.jpg";
import fitted_midfidelity9 from "../../assets/img/fitted_midfidelity9.jpg";
import fitted_midfidelity10 from "../../assets/img/fitted_midfidelity10.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: fitted_midfidelity1},
        {img: fitted_midfidelity2},
        {img: fitted_midfidelity3},
        {img: fitted_midfidelity4},
        {img: fitted_midfidelity5},
        {img: fitted_midfidelity6},
        {img: fitted_midfidelity7},
        {img: fitted_midfidelity8},
        {img: fitted_midfidelity9},
        {img: fitted_midfidelity10},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "auto",
      },
    };
  }

};
</script>

