<template>
  <div>
    <Carousel
      v-bind:items="items"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import LineBreak from '../common/LineBreak';

import fitted_header_version1 from "../../assets/img/fitted_header_version1.png";
import fitted_mockup4 from "../../assets/img/fitted_mockup4.png";
import fitted_mockup2 from "../../assets/img/fitted_mockup2.png";
import fitted_mockup3 from "../../assets/img/fitted_mockup3.png";
import fitted_mockup1 from "../../assets/img/fitted_mockup1.png";

export default {
  components: {
    Carousel,
    LineBreak,
  },
  data() {
    return {
      items: [
        {img: fitted_header_version1},
        {img: fitted_mockup4},
        {img: fitted_mockup3},
        {img: fitted_mockup2},
        {img: fitted_mockup1},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



